  margin-left: 10px;
  font-size: 400%;
  line-height: 0;
  position: absolute;
  top: 45%;
  left: 0;
  transform: translateY(-50%);
}

@keyframes panoramic-left-arrow-animation {
  0% {
    transform: translateX(0);
  }

  50% {
    transform: translateX(10px);
  }

  100% {
    transform: translateX(0);
  }
}

@keyframes panoramic-right-arrow-animation {
  0% {
    transform: translateX(0);
  }

  50% {
    transform: translateX(-10px);
  }

  100% {
    transform: translateX(0);
  }
}

#panoramic-left-arrow {
  animation: 2s infinite panoramic-left-arrow-animation;
}

#panoramic-right-arrow {
  animation: 2s infinite panoramic-right-arrow-animation;
}

